import {
	registerurl,
	getVeriCode,
	myrequest
} from '../../api.js';

export default {
	data() {
		return {
			mobile: '',
			password: '',
			veriCode: '',
			res: '',
			va:{
				title: "获取验证码",
				click: true,
			}
		}
	},
	onLoad() {

	},
	methods: {
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
			if (key == 'password') {
				if (this['mobile'].length != 11) {
					uni.showToast({
						title: '电话长度不等于11位,请重新输入',
						icon: 'none'
					})
				}
			} else if (key == 'repassword') {
				if (this['password'].length < 8) {
					uni.showToast({
						title: '密码的长度要大于八位',
						icon: 'none'
					})
				}
			}
		},
		async getVeriCode(){
			const sendData = {
				name: this.mobile
			};
			console.log("获取验证码");
			const result = await myrequest(getVeriCode, 'POST', sendData);
			console.log(result);
		},
		async toRegister() {
			if (this['repassword'] != this['password']) {
				uni.showToast({
					title: '两次输入密码不一致',
					icon: 'none'
				})
				return 0;
			}
			const {
				mobile,
				password
			} = this;

			const sendData = {
				name: mobile,
				password,
				veriCode: this.veriCode
			};
			const result = await myrequest(registerurl, 'POST', sendData);
			if (result['respCode'] == 0) {
				uni.showToast({
					title: '注册成功，跳转到登录页面',
					icon: 'none',
				})
				uni.navigateBack();
			}
		}
	},

}
